  border-radius: 3px;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes pulse-slow {
  0%,
  100% {
    opacity: 0.4;
  }
  50% {
    opacity: 1;
  }
}
.fade-in {
  animation: fadeIn 0.3s ease;
}
.pulse-slow {
  animation: pulse-slow 2s ease-in-out infinite;
}
.body-scroll-lock {
  overflow: hidden;
}
